import {useState} from 'react'
import { StarOutlined, StarFilled, StarTwoTone,NodeIndexOutlined,FundOutlined,PlusOutlined,LinkOutlined} from '@ant-design/icons';
import { Calendar,ConfigProvider } from 'antd';
import zhCN from 'antd/lib/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';

import './approve.css'
import './announcement.css'
function Announcement(){
    const [arr,setarr] = useState(JSON.parse(localStorage.announcement))
    console.log(arr);
    const [arr2,setarr2] = useState([
        {title:"公告中"},
        {title:"已结束"},
    ])
    const [arr2_index,setarr2_index] = useState(0)
    function approve_box__qx(){
        setapprove_box_qx(0)
    } 

    const [approve_title_1,setapprove_title_1] = useState(1)
    // const [input_none4,setinput_none4] = useState(1)
    function approveTitle_1(){
        setapprove_title_1(1)
        
    }
    function approveTitle_2(){
        setapprove_title_1(2)
      
    }
    const [input_value2,setinput_value2] = useState('')
    const [input_none2,setinput_none2] = useState(1)
    const [input_none3,setinput_none3] = useState(1)
    const [input_none4,setinput_none4] = useState(1)
    // function inputValue(event){
    //     let a = event.target.value
    //     console.log(a);
    //     setinput_value(arr[input_value_index].title)
    // }
    function approve_box_btn_select_input2(){
        if(input_none2===1){
            setinput_none2(2)
        }else{
            setinput_none2(1)
        }
        // console.log(input_none);
        
    }
    function approve_box_btn_select_input3(){
        if(input_none3===1){
            setinput_none3(2)
        }else{
            setinput_none3(1)
        }
        // console.log(input_none);
        
    }
    function approve_box_btn_select_input4(){
        if(input_none4===1){
            setinput_none4(2)
        }else{
            setinput_none4(1)
        }
        // console.log(input_none);
        
    }

    function input_list2(index){
        setinput_value2(arr2[index].title)
        setinput_none2(1)
        setarr2_index(index)
        console.log(arr2_index);
        

    }
    function onPanelChange(value, mode) {
        console.log(value, mode);
      }
      const [approve_box_qx,setapprove_box_qx] = useState(1)
    function approve_box__qx(){
        setapprove_box_qx(2)
    } 
    const [qx_el_card__body,setqx_el_card__body] = useState(1)
    function funqx_el_card__body(){
        
        if(qx_el_card__body===1){
            setqx_el_card__body(2)
        }else{
            setqx_el_card__body(1)
        }
        setapprove_box_qx(1)
    }
    function formatDate(now) { 
        var now= new Date(now*1000);
        var year=now.getFullYear(); 
        var month=now.getMonth()+1; 
        var date=now.getDate(); 
        var hour=now.getHours(); 
        var minute=now.getMinutes(); 
        var second=now.getSeconds(); 
        return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second; 
   } 
    return(
        
        <div className='approve_box'>
            <div className="approve_box_w">
                <div className="approve_box_title clearfix announcement_po">
                    <ul className="approve_box_title__active-bar">
                        <li className="approve_box_title__active-bar_color">
                            公告
                            <span></span>
                        </li>
                    </ul>
                        <div className="approve_box_btn-select_xj announcement_po_ab" onClick={approve_box__qx}>新建公告</div>
                </div>
                <div >
                    <div className="select-group">
                        <div className="select-group-sh">公告状态</div>
                        <div className={approve_title_1===1?'approve_box_btn-select_input el-input--suffix-block  announcement_po_wit':'approve_box_btn-select_input  el-input--suffix-none announcement_po_wit'}>
                            <input type="text" value={input_value2} onClick={approve_box_btn_select_input2} placeholder="公告中" />
                            {/* onChange={inputValue} */}
                            <div className={input_none2===2?'approve_box_btn-select_input_list  approve_box_btn-select_input_list_block announcement_po_bott':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none announcement_po_bott'}>
                                
                                {
                                    arr2.map((item,index)=>{
                                        return(
                                            <div key={index}>
                                                <div className="approve_box_btn-select_input_list_sm" onClick={()=>{input_list2(index)}}>{item.title}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <span className={input_none2===1?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe61f;</span>
                            <span className={input_none2===2?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe689;</span>
                        </div>
                    </div>
                    <div className={arr2_index===0?'examine-list-boxmy_content examine-list-boxmy_content_none':'examine-list-boxmy_content examine-list-boxmy_content_block'}>
                        {
                            arr.map((item,index)=>{
                                return(
                                    <div className="examine-list-boxmy_content_list-box">
                                        <div className="boxmy_content_list-box_xx">
                                            <div className="boxmy_content_list-box_xx_img">
                                                <img src={item.thumb_img}/>
                                            </div>
                                            <div className="boxmy_content_list-box_xx_title">
                                                <div className="boxmy_content_list-box_xx_title_realname">{item.realname}</div>
                                                <div className="boxmy_content_list-box_xx_title_start_time">{formatDate(item.start_time)}</div>
                                            </div>
                                        </div>
                                        <div className="boxmy_content_list-box_title">{item.title}</div>
                                        <div className="boxmy_content_list-box_content">{item.content}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div className='examine-list-boxmy'>没有更多了</div>  
                </div>   
                <div className={approve_box_qx===2?'qx_el-card__body  qx_el-card__body_didlock':'qx_el-card__body'}>
                    <div className="qx_el-card__body_box">
                        <h3 className='approve_box_bac_wigth_h3'>新建公告<span className="iconfont" onClick={funqx_el_card__body}>&#xe63f;</span></h3>
                        <div className="qx_el-card__body_content">
                            <div>
                                <div className="qx_el-card__body_crm-create ">
                                    <div className="qx_el-card__body_crm-create_title crm-create_title_rig el-textarea_box">
                                        <h6><span>*</span>公告标题</h6>
                                        <div className="qx_el-card__body_crm-create_input input_line_h_l">
                                            <input type="text" />
                                        </div>
                                    </div>
                                    <div className="qx_el-card__body_crm-create_title crm-create_title_lef el-textarea_box">
                                        <h6>通知部门</h6>
                                        <div className="qx_el-card__body_crm-create_input input_line_h_l">
                                            <input type="text" />
                                        </div>
                                    </div>
                                </div>
                                <div className="clearfix is-success_el-form-itemstart_time">
                                    <div className="approve_box_btn-select_inputrili el-form-itemstart_time_flot_l el-textarea_box">
                                        <h6>开始时间</h6>
                                        <div onClick={approve_box_btn_select_input3} className="select-group-rq">
                                            <div className="iconfont">&#xe6f5;</div>
                                            <input type="text" placeholder="选择日期"/>
                                        </div>
                                        <div className={input_none3===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none'}>
                                            <ConfigProvider locale={zhCN}>
                                                <div className="site-calendar-demo-card">
                                                    <Calendar fullscreen={false} onPanelChange={onPanelChange} />
                                                </div>
                                            </ConfigProvider>
                                        </div>
                                    </div>
                                    <div className="approve_box_btn-select_inputrili el-form-itemstart_time_flot_r el-textarea_box">
                                        <h6>结束时间</h6>
                                        <div onClick={approve_box_btn_select_input4} className="select-group-rq">
                                            <div className="iconfont">&#xe6f5;</div>
                                            <input type="text" placeholder="结束日期"/>
                                        </div>
                                        <div className={input_none4===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none'}>
                                            <ConfigProvider locale={zhCN}>
                                                <div className="site-calendar-demo-card">
                                                    <Calendar fullscreen={false} onPanelChange={onPanelChange} />
                                                </div>
                                            </ConfigProvider>
                                        </div>
                                    </div>
                                </div> 
                                <div className="qx_el-card__body_crm_create_input_bo el-textarea_box">
                                    <h6><span>*</span>公告正文</h6>
                                    <div className="qx_el-card__body_crm_create_input_box el-textarea">
                                        <input type="text" placeholder="请输入内容" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="qx_el-card__body_crm_create_button">
                            <div className="qx_el-card__body_crm_create_button_l"  onClick={funqx_el_card__body}>保存</div>
                            <div className="qx_el-card__body_crm_create_button_r"  onClick={funqx_el_card__body}>取消</div>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}
export default Announcement