import './customertotal.css'
import './dashboard.css'
import {useState,useRef,useEffect} from 'react'
import * as echarts from 'echarts';
import axios from 'axios'

function Customertotal(){

    const [customertotalarr,setcustomertotalarr] = useState(JSON.parse(localStorage.customertotal))
    const [customertotalarrs,scustomertotalarrs] =useState([JSON.parse(localStorage.dashboarda)])
    
    let b = customertotalarr
    let customertotalarrso = customertotalarrs[0]
    console.log(customertotalarrso);
    
    console.log(b);
    
    let c = []

    for (let i = 0; i < b.length; i++) {
        c.push(b[i].customer_num)
    }
    let d = []
    for (let i = 0; i < b.length; i++) {
        d.push(b[i].deal_customer_num)
    }
    let e = []
    for (let i = 0; i < customertotalarrso.length; i++) {
        e.push(customertotalarrso[i].type)
    }
    console.log(c,d,e);
    
    const [todolist_scrollbar_1,settodolist_scrollbar_1] = useState(1)
    function  onTodolist_scrollbar_1(){
       if(todolist_scrollbar_1===1){
            settodolist_scrollbar_1(2)
       }else{
            settodolist_scrollbar_1(1)
       } 
    }
    const dashboard = [
        {id:0,title:'今天'},
        {id:2,title:'昨天'},
        {id:2,title:'本周'},
        {id:2,title:'上周'},
        {id:2,title:'本月'},
        {id:2,title:'上月'},
        {id:2,title:'上季度'},
        {id:2,title:'本年'},
        {id:2,title:'去年'},
        {id:2,title:'自定义'},
    ]
    const [dashboardcolor,setdashboardcolor] = useState(0)
    const [todolist_option_bar_value_1,settodolist_option_bar_value_1] = useState(dashboard[0].title)
    function todolist_option_bar_setvalue_1(index){
        settodolist_option_bar_value_1(dashboard[index].title)
        setdashboardcolor(index)
    }
    
    const dashboardd = [
        {id:0,title:'办公室'},
        {id:2,title:'打杂部'},
    ]
    const [todolist_scrollbar_11,settodolist_scrollbar_11] = useState(1)
    function  onTodolist_scrollbar_11(){
       if(todolist_scrollbar_11===1){
            settodolist_scrollbar_11(2)
       }else{
            settodolist_scrollbar_11(1)
       } 
    }
    const [todolist_option_bar_value_11,settodolist_option_bar_value_11] = useState(dashboardd[0].title)
    const [dashboardcolor1,setdashboardcolor1] = useState(0)
    function todolist_option_bar_setvalue_11(index){
        settodolist_option_bar_value_11(dashboardd[index].title)
        setdashboardcolor1(index)
    }
    const [customertotal2,setcustomertotal2] = useState(JSON.parse(localStorage.sb))
    console.log(customertotal2)
    let customertotal1 = []
    if(customertotal2.length>0){
        for (let i = 0; i < customertotal2[0].userList.length; i++) {
            customertotal1.push(customertotal2[0].userList[i].realname)
        }
    }
    console.log(customertotal1);
    const [todolist_scrollbar_12,settodolist_scrollbar_12] = useState(1)
    function  onTodolist_scrollbar_12(){
       if(todolist_scrollbar_12===1){
            settodolist_scrollbar_12(2)
       }else{
            settodolist_scrollbar_12(1)
       } 
    }
    const [todolist_option_bar_value_12,settodolist_option_bar_value_12] = useState('选择员工')
    const [dashboardcolor2,setdashboardcolor2] = useState(0)
    function todolist_option_bar_setvalue_12(index){
        settodolist_option_bar_value_12(customertotal1[index])
        setdashboardcolor2(index)
    }
    
    const refs = useRef(null)
    useEffect(() => {
        // console.log(refs.current);
        const myChart = echarts.init(refs.current);
        const option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {     
                    type: 'shadow' 
                }
            },
            legend: {
                data: ['成交客户数', '新增客户数',],
                bottom: 20,
            },
            xAxis: [
                {
                    type: 'category',
                    axisTick: {
                        alignWithLabel: true
                    },
                    data: e,
                    axisLine: {
                        show: true,
                        lineStyle: {
                            color: '#c1c1c1'
                        }
                    },
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    name: '新增客户数',
                    position: 'r',
                    axisLine: {
                        show: true,
                        lineStyle: {
                            color: '#c1c1c1'
                        }
                    },
                }
            ],
            series: [
                {
                    name: '成交客户数',
                    type: 'bar',
                    data: c,
                    itemStyle: {
                        color: '#6ca2ff'
                    }
                },
                {
                    name: '新增客户数',
                    type: 'bar',
                    data: d,
                    itemStyle: {
                        color: '#ff7f7f'
                    }
                },
            ]
        };
        option && myChart.setOption(option);
      })
    return(
        <div className="customertotal">
            <div className="customertotal-box clearfix">
                <div className="customertotal-xlk dashboard-title-xlk todolist_cont_detail_option-bar option-bar-width-1" onClick={onTodolist_scrollbar_1}>
                    <input value={todolist_option_bar_value_1}/>
                    <div className="todolist_cont_detail_option-bar-ic">
                        <span className="iconfont" style={{display:todolist_scrollbar_1===1?'block':'none'}}>&#xe61f;</span>
                        <span className="iconfont" style={{display:todolist_scrollbar_1===2?'block':'none'}}>&#xe689;</span>
                    </div>
                    <div className="dashboard-title-xlk-tc todolist_cont_input_el-scrollbar input_el-scrollbar_bottom_1 " style={{display:todolist_scrollbar_1===2?'block':'none'}}>
                        {
                            dashboard.map((ite,index)=>{
                                return(
                                    <div key={index}>
                                        <div onClick={()=>{todolist_option_bar_setvalue_1(index)}} style={{color:dashboardcolor===index?'#409eff':'#000 ',fontWeight:dashboardcolor===index?'bold':''}} className="dashboard-title-xlk-tc-sj-span todolist_cont_input_el-scrollbar_title"><span style={{display:dashboardcolor===index?'block':'none'}}></span>{ite.title}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <div className="customertotal-xlkk dashboard-title-xlk todolist_cont_detail_option-bar option-bar-width-1" onClick={onTodolist_scrollbar_11}>
                    <input value={todolist_option_bar_value_11}/>
                    <div className="todolist_cont_detail_option-bar-ic">
                        <span className="iconfont" style={{display:todolist_scrollbar_11===1?'block':'none'}}>&#xe61f;</span>
                        <span className="iconfont" style={{display:todolist_scrollbar_11===2?'block':'none'}}>&#xe689;</span>
                    </div>
                    <div className="customertotal-title-xlk-tc1 customertotal-title-xlk-tc todolist_cont_input_el-scrollbar input_el-scrollbar_bottom_1" style={{display:todolist_scrollbar_11===2?'block':'none'}}>
                        {
                            dashboardd.map((ite,index)=>{
                                return(
                                    <div key={index}>
                                        <div onClick={()=>{todolist_option_bar_setvalue_11(index)}} style={{color:dashboardcolor1===index?'#409eff':'#000 ',fontWeight:dashboardcolor1===index?'bold':''}} className="dashboard-title-xlk-tc-sj-span todolist_cont_input_el-scrollbar_title"><span style={{display:dashboardcolor1===index?'block':'none'}}></span>{ite.title}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <div className="customertotal-xlkk dashboard-title-xlk todolist_cont_detail_option-bar option-bar-width-1" onClick={onTodolist_scrollbar_12}>
                    <input value={todolist_option_bar_value_12}/>
                    <div className="todolist_cont_detail_option-bar-ic">
                        <span className="iconfont" style={{display:todolist_scrollbar_12===1?'block':'none'}}>&#xe61f;</span>
                        <span className="iconfont" style={{display:todolist_scrollbar_12===2?'block':'none'}}>&#xe689;</span>
                    </div>
                    <div className="customertotal-title-xlk-tc1 dashboard-title-xlk-tc todolist_cont_input_el-scrollbar input_el-scrollbar_bottom_1" style={{display:todolist_scrollbar_12===2?'block':'none'}}>
                        {
                            customertotal1.map((ite,index)=>{
                                return(
                                    <div key={index}>
                                        <div onClick={()=>{todolist_option_bar_setvalue_12(index)}} style={{color:dashboardcolor2===index?'#409eff':'#000 ',fontWeight:dashboardcolor2===index?'bold':''}} className="dashboard-title-xlk-tc-sj-span todolist_cont_input_el-scrollbar_title"><span style={{display:dashboardcolor2===index?'block':'none'}}></span>{ite}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <button className="customertotal-ti-btn">搜索</button>
            </div>
            <div className="customertotal-content-no">
                <div className="customertotal-content-n" ref={refs}>
                </div>
                <div>
                    <div className="customertotal-content-bg-w">
                        <div className="customertotal-content-bg customertotal-content-bg-c clearfix">
                            <div className="l">
                                员工姓名
                            </div>
                            <div className="l">
                                新增客户数
                            </div>
                            <div className="l">
                                成交客户数
                            </div>
                            <div className="l">
                                客户成交率(%)
                            </div>
                            <div className="l">
                                合同总金额
                            </div>
                            <div className="l">
                                回款金额
                            </div>
                            <div className="l">
                                未回款金额
                            </div>
                            <div className="h">
                                回款完成率(%)
                            </div>
                        </div>
                        {customertotalarr.map((item,index)=>{
                            return(
                                <div key={index}>
                                    <div className="customertotal-content-bg clearfix">
                                        <div className="l">
                                            {item.realname}
                                        </div>
                                        <div className="l">
                                            {item.customer_num}
                                        </div>
                                        <div className="l">
                                            {item.deal_customer_num}
                                        </div>
                                        <div className="l">
                                            {item.contract_money}
                                        </div>
                                        <div className="l">
                                            {item.deal_customer_rate}
                                        </div>
                                        <div className="l">
                                            {item.receivables_money}
                                        </div>
                                        <div className="l">
                                            {item.un_receivables_money}
                                        </div>
                                        <div className="h">
                                            {item.deal_receivables_rate}
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Customertotal