import axios from 'axios'
import '../components/schedule.css'
import { useState } from 'react';
import Time from '../components/rl_time';
import 'antd/dist/antd.css';
import ScheduleCreate from '../components/schedule_create';
import {ConfigProvider, Calendar, Alert, Input } from 'antd';
import zhCN from 'antd/lib/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';
import '../components/schedule.css'
function Jobscalendar(props){
    moment.locale('zh-cn');
    axios.post('http://crm.cimns.com/index.php/admin/users/getUserList',{
        type:0,
        page:1,
        limit:15
        }).then((data)=>{
            const popsnum = data.data.data
            console.log(popsnum)
        },(err)=>{
            console.log(err)
        })
        const [state,setstate] = useState({
            value: moment('2021-4'),
            selectedValue: moment('2021-4'),
        })
        function onSelect(value){
            setstate({
                value,
                selectedValue: value,
            })
        }
        function onPanelChange(value){
            setstate({ value });
        }
        const { value, selectedValue } = state;
        const [num,setnum] = useState(0)
        function create_btn(){
            setnum(1)
            // console.log(num);
        }
        function create_btn2(){
            setnum(0)
            // console.log(num);
        }

        const [arr,setarr] = useState(JSON.parse(localStorage.sb))
        console.log(arr[0].userList)
        const [arrnum,setarrnum] = useState([
            {id:1,color:'#3E8EF7'},
            {id:2,color:'#11C06C'},
            {id:3,color:'#0BB2D4'},
            {id:4,color:'#EB6709'},
            {id:5,color:'#FF4C52'},
            {id:6,color:'#526069'},
            {id:7,color:'#9262F4'},
        ])
        const [ccc,setccc] = useState(0)
        const [btnsnum,setbtnsnum] = useState(0)
        function create_btns(index){
            setccc(index)
        }
        function btnsss(){
            if (btnsnum===0) {
                setbtnsnum(1)
            }else{
                setbtnsnum(0)
            }
        }
        const [inpu,setinpu] = useState(1)
        function oninpu(){
            if(inpu===1){
                setinpu(2)
            }else{
                setinpu(1)
            }
        }
        const { TextArea } = Input;
        const [back,setback] = useState(1)
        const [backa,setbacka] = useState(1)
        const [backs,setbacks] = useState(1)
        const [backd,setbackd] = useState(1)
    
        function task_aaa(){
            if(back===1){
                setback(2)
                setbacka(1)
                setbacks(1)
                setbackd(1)
            }else{
                setback(1)
            }
        }
        function task_bbb(){
            if(backs===1){
                setbacks(2)
                setback(1)
                setbacka(1)
                setbackd(1)
            }else{
                setbacks(1)
                
            }
        }
        function task_ccc(){
            if(backd===1){
                setbackd(2)
                setback(1)
                setbacka(1)
                setbacks(1)
                
            }else{
                setbackd(1)
    
            }
        }
        function task_ddd(){
            if(backa===1){
                setbacka(2)
                setback(1)
                
                setbacks(1)
                setbackd(1)
            }else{
                setbacka(1)
    
            }
        }
        return (
        <div className="sch_ww_box clearfix">
            <div className="schedule">
                <div className="schedule_list">
                        <ConfigProvider locale={zhCN}>
                            <div className="schedule_po">
                                <div className="schedule_rili">
                                    <div>今天</div>
                                </div>
                                <Alert  message={`${selectedValue && selectedValue.format('YYYY-MM')}`}/>
                                <div className="creat_rc" onClick={create_btn}>创建日程</div>
                            </div>
                            <Calendar value={value} onSelect={onSelect}  onPanelChange={onPanelChange}/>
                        </ConfigProvider>
                </div>
            </div>
            <div className={num===1?'cent_box':'cent_box_hidden'}>
                <div className="nr">
                    <div className="work_tianjia">
                        <div className="nr_box">
                            <div className="header_box">
                                <h4>新建任务</h4>
                                <div className="work_liter_x">
                                    <div className="rw">
                                        <div className="one">
                                            <h6>任务名称</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                        <div className="one">
                                            <h6>负责人</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                    </div>
                                    <div className="work_ch clearfix">
                                        <div className="create_lefts">
                                            <p className="create_div_p"><span className="create_div_span">*</span>开始时间</p>
                                            <Time></Time>
                                        </div>
                                        <div className="create_rights">
                                            <p className="create_div_p"><span className="create_div_span">*</span>结束时间</p>
                                            <Time></Time>
                                        </div>
                                    </div>
                                    <div className="you">
                                        <h6 className="you_ki">优先级</h6>
                                        <span onClick={task_aaa} className={back===2?'hong bg-h':'hong'} >高</span>
                                        <span onClick={task_bbb} className={backs===2?'hong bg-c':'cheng'}>中</span>
                                        <span onClick={task_ccc} className={backd===2?'hong bg-l':'lan'}>低</span>
                                        <span onClick={task_ddd} className={backa===2?'hong bg-hui':'hui'}>无</span>
                                    </div>
                                    <div className="ms">
                                        <h6 className="ma_se">任务描述</h6>
                                    </div>
                                    <textarea autoComplete="off" className="wokr_cll"></textarea>
                                    <div className="gl">
                                        <p>关联业务</p>
                                    </div>
                                </div>
                                <span onClick={create_btn2} className="iconfont create_icon">&#xe63f;</span>
                                <div className="create_bc">
                                    <div className="create_bcqx1">保存</div>
                                    <div onClick={create_btn2} className="create_bcqx2">取消</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Jobscalendar