import {useState} from 'react'
import './recycle.css'
function Recycle(){
    const recycle= JSON.parse(localStorage.recycle)
    console.log(recycle);
    const [recindex,srecindex] = useState(0)
    const [recxs,srecxs] = useState(0)
    function formatDate(now) { 
        var now= new Date(now*1000);
        var year=now.getFullYear(); 
        var month=now.getMonth()+1; 
        var date=now.getDate(); 
        var hour=now.getHours(); 
        var minute=now.getMinutes(); 
        var second=now.getSeconds(); 
        return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second; 
   } 
   function btn(index){
    srecindex(index)
    srecxs(1)
    console.log(index);
    console.log(recycle[index]);
   }
   function btnn(){
    srecxs(0)
   }
    return(
        <div className="recycle-box">
            <h3 className="recycle-h3">回收站</h3>
            <div className="recycle-cnt">
                {recycle.map((item,index)=>{
                    return(
                        <div key={index}>
                            <div className="recycle-ct-inp clearfix">
                                <input type="checkbox" checked ='false'/>
                                <div className="clearfix" onClick={()=>{btn(index)}}>
                                    <div className="recycle-ct-inp-bt">
                                        {item.name}
                                    </div>
                                    <div className="recycle-ct-inp-r">
                                        <span className="iconfont">&#xe66d;</span>
                                        <div className="recycle-ct-inp-sj">
                                            {formatDate(item.create_time)}截止
                                        </div>
                                        <div className="recycle-ct-inp-sj recycle-ct-inp-img">
                                            {item.thumb_img?(
                                                <img src={item.thumb_img}/>
                                            ):<div className="recycle-ct-inp-img-txt">管理</div>}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
            <div className="recycle-fixed" style={{display:recxs===0?'none':'block'}}>
                <div className="recycle-fixed-tb clearfix">
                    <div className="recycle-fixed-tb-r">
                        <ul>
                            <li>
                                <span className="iconfont">&#xe617;</span>优先级
                            </li>
                            <li>
                                <span className="iconfont">&#xe66c;</span>标签
                            </li>
                            <li>
                                <span className="iconfont">&#xe637;</span>任务
                            </li>
                            <li>
                                <span className="iconfont">&#xe71a;</span>附件
                            </li>
                        </ul>
                        <span className="iconfont" onClick={btnn}>&#xe63f;</span>
                    </div>
                </div>
                <div className="recycle-fixed-ct">
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l clearfix">
                            <input type="checkbox" checked/><span>{recycle[recindex].name}</span>
                        </div>
                        <div className="recycle-fixed-ct-r clearfix">
                            <p className="recycle-fixed-ct-r-p">负责人：</p>{recycle[recindex].thumb_img?(
                                        <img src={recycle[recindex].thumb_img}/>
                                    ):<div className="recycle-ct-inp-img-txt recycle-fixed-ct-r-gl">管理</div>}
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l">
                            <span className="recycle-fixed-ct-l-bq">hgfh</span>
                        </div>
                        <div className="recycle-fixed-ct-r">
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l">
                            <p>暂无描述</p><span className="iconfont">&#xe711;</span><span className="recycle-fixed-ct-l-span">暂无描述</span>
                        </div>
                        <div className="recycle-fixed-ct-r">
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l">
                            <p>参与人：</p><span className="recycle-fixed-ct-l-qwe"><em>+</em></span>
                        </div>
                        <div className="recycle-fixed-ct-r">
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l">
                        <span className="iconfont">&#xe6ea;</span><p>关联业务</p>
                        </div>
                        <div className="recycle-fixed-ct-r">
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-r recycle-fixed-ct-r-b-ys">
                            <span className="iconfont">&#xe71a;</span><p>关联业务</p>
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-l">
                            <span className="iconfont">&#xe6ea;</span><p>子任务(0/0)</p>
                            <div className="recycle-fixed-ct-l-bka"></div>
                        </div>
                    </div>
                    <div className="recycle-fixed-ctt">
                        <div className="recycle-fixed-ct-r recycle-fixed-ct-r-b-ys">
                            <span className="iconfont">&#xe711;</span><p>子元素任务</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Recycle