
import {useState} from 'react'
import {UpOutlined,DownOutlined } from '@ant-design/icons';
import './salesfunnel.css'
function Productsvolume(){
    const [salesfunnel_years,getsalesfunnel_years] = useState(0)
    function salesfunnelYears(){
        if(salesfunnel_years===0){
            getsalesfunnel_years(1)
        }else{
            getsalesfunnel_years(0)
        }
    }
    const [salesfunnel_classification,getsalesfunnel_classification] = useState(0)
    function salesfunnelClassification(){
        if(salesfunnel_classification===0){
            getsalesfunnel_classification(1)
        }else{
            getsalesfunnel_classification(0)
        }
    }
    const [salesfunnel_staff,getsalesfunnel_staff] = useState(0)
    function salesfunnelStaff(){
        if(salesfunnel_staff===0){
            getsalesfunnel_staff(1)
        }else{
            getsalesfunnel_staff(0)
        }
    }
    const arr = JSON.parse(localStorage.sb)
    const salesfunnel_content_title= 
        {
            years:[
                {id:0,title:'今天'},
                {id:1,title:'昨天'},
                {id:2,title:'本周'},
                {id:3,title:'上周'},
                {id:4,title:'本月'},
                {id:5,title:'上月'},
                {id:6,title:'本季度'},
                {id:7,title:'上季度'},
                {id:8,title:'本年'},
                {id:9,title:'去年'},
                {id:10,title:'自定义'},
            ],
            classification:[
                {id:0,title:'办公室'},
                {id:1,title:'打杂部'},
            ],
            system:[
                {id:0,title:"系统默认"}
            ]


        }
    // console.log(arr[0].userList);
    const [years_list_val,setyears_list_val] = useState('本月')
    const [years_list_color,setyears_list_color] = useState(4)
    function  yearsList(index){
        setyears_list_val(salesfunnel_content_title.years[index].title)
        setyears_list_color(index)
        getsalesfunnel_years(0)
    }
    const [staff_list,setstaff_list] = useState('办公室')
    const [staff_list_color,setstaff_list_color] = useState(1)
    function onstaffList(index){
        setstaff_list(salesfunnel_content_title.classification[index].title)
        getsalesfunnel_classification(0)
        setstaff_list_color(index)
    }
    const [personnel_list,setpersonnel_list] = useState('')
    const [personnel_list_color,setpersonnel_list_color] = useState(0)
    function personnelList(index){
        setpersonnel_list( arr[0].userList[index].realname)
        setpersonnel_list_color(index)
        getsalesfunnel_staff(0)
    }
  return(
        <div className="salesfunnel_box">
            <div className="salesfunnel_content_box">
                <div className="salesfunnel_content productssales_constbox">
                    <div className="salesfunnel_content_title clearfix">
                        <div className="salesfunnel_title_years_box">
                            <div className="salesfunnel_title_years" onClick={salesfunnelYears}>
                                <input type="text" value={years_list_val}/>
                                <span style={{display:salesfunnel_years===0?'none':'block'}}>
                                    <UpOutlined />
                                </span>
                                <span style={{display:salesfunnel_years===1?'none':'block'}}>
                                    <DownOutlined />
                                </span>
                            </div>
                            <div className="years_list_box" style={{display:salesfunnel_years===0?'none':'block'}}>
                                {
                                        salesfunnel_content_title.years.map((item,index)=>{
                                            return(
                                                
                                                <div className="years_list" key={index}>
                                                    <div onClick={()=>{yearsList(index)}} style={{color:years_list_color===index?'#409eff':'#333'}}><span style={{display:years_list_color===index?'block':'none'}}></span>{item.title}</div>
                                                </div>
                                                
                                            )
                                        })
                                }
                            </div>
                        </div>
                        <div className="salesfunnel_title_staff_box">
                            <div  className="salesfunnel_title_classification" onClick={salesfunnelClassification}>
                                    <input type="text" value={staff_list}/>
                                    <span style={{display:salesfunnel_classification===0?'none':'block'}}>
                                        <UpOutlined />
                                    </span>
                                    <span style={{display:salesfunnel_classification===1?'none':'block'}}>
                                        <DownOutlined />
                                    </span>
                            </div>
                            <div className="staff_list_box" style={{display:salesfunnel_classification===0?'none':'block'}}>
                                {
                                    salesfunnel_content_title.classification.map((item,index)=>{
                                        return(
                                            <div className="staff_list">
                                                <div onClick={()=>{onstaffList(index)}} style={{color:staff_list_color===index?'#409eff':'#333'}}>{item.title}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                       <div className="salesfunnel_title_staff_box">
                            <div className="salesfunnel_title_classification salesfunnel_title_staff" onClick={salesfunnelStaff}>
                                <input placeholder="选择员工" value={personnel_list}/>
                                <span style={{display:salesfunnel_staff===0?'none':'block'}}>
                                    <UpOutlined />
                                </span>
                                <span style={{display:salesfunnel_staff===1?'none':'block'}}>
                                    <DownOutlined />
                                </span>
                            </div>
                            <div className="personnel_list_box" style={{display:salesfunnel_staff===0?'none':'block'}}>
                                {
                                    arr[0].userList.map((item,index)=>{
                                        return(
                                            <div className="personnel_list">
                                                <div onClick={()=>{personnelList(index)}} style={{color:personnel_list_color===index?'#409eff':'#333'}}>{item.realname}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                        <div className="salesfunnel_title_search">
                            <button>搜索</button>
                        </div>
                    </div>
                    <div className="salesfunnel_list_content productssales_box">
                        <div className="product_classification_sales">
                            产品分类销售
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Productsvolume