import React from 'react';
import {useState} from 'react'
import { Menu, Button } from 'antd';
import Customertotal from './customertotal'
import Customerfollows from './customerfollows'
import Customeratom from './customeratom'
import Customerrate from './customerrate'
import Customerseas from './customerseas'
import Customerdeal from './customerdeal'
import Salesfunnel from './salesfunnel'
import Salesrenewal from './salesrenewal'
import Salesrate from './salesrate'
import Employeenumber from './employeenumber'
import Employeecontract from './employeecontract'
import Employeeamount from './employeeamount'
import Employeereceivable from './employeereceivable'
import Customersurban from './customersurban'
import Customersindustry from './customersindustry'
import Customerslevel from './customerslevel'
import Customerssource from './customerssource'
import Listcontract from './listcontract'
import Listamount from './listamount'
import Listofferid from './listofferid'
import Listsales from './listsales'
import Listnewclient from './listnewclient'
import Listnewcontact from './listnewcontact'
import Listnumber from './listnumber'
import Listfollows from './listfollows'
import Listtrips from './listtrips'
import Productssales from './productssales'
import Productsvolume from './productsvolume'
import Officelog from './officelog'
import Officeapproval from './officeapproval'
import Browserperformance from './browserperformance'
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    TeamOutlined,
    FilterOutlined,
    FundOutlined,
    UserAddOutlined,
    AppstoreOutlined,
    BarChartOutlined,
    LineChartOutlined,

}  from '@ant-design/icons';
import './busines.css'
import { Route, Switch } from 'react-router';
import { Link } from 'react-router-dom';
const { SubMenu } = Menu;
function Busines(){
    const [state,setstate] = useState({collapsed: false,})
    const [css,setcss] = useState(1)
    function toggleCollapsed(){
        setstate(
            {collapsed: !state.collapsed,}
        )
        if(css===1){
            setcss(2)
        }else{
            setcss(1)
        }
    }
    return(
        <div className="hbfb clearfix">
            <div className={css===1?'bag':'bagact'}>
                <div className="bus-gdt">
                <Menu className="busines-ul-uu ul" defaultSelectedKeys={['1']} defaultOpenKeys={['sub1']} mode="inline" theme="dark" inlineCollapsed={state.collapsed}>
                    <SubMenu key="sub1" icon={<TeamOutlined />} title="员工客户分析">
                        {/* <Link to="/App/Client/" className="a"> 
                            Customertotal
                            Customerfollows
                            Customeratom
                            Customerrate
                            Customerseas
                            Customerdeal
                        </Link> */}
                        <Menu.Item key="1">客户总量分析
                            <Link to="/App/Busines" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="2">客户跟进次数分析
                            <Link to="/App/Busines/Customerfollows" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="3">客户跟进方T
                            <Link to="/App/Busines/Customeratom" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="4">客户转化率分析
                            <Link to="/App/Busines/Customerrate" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="5">公海客户分析
                            <Link to="/App/Busines/Customerseas" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="6">成交周期分析
                            <Link to="/App/Busines/Customerdeal" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub2" icon={<FilterOutlined />} title="销售漏斗分析">
                        {/* <Link to="/App/Client/" className="a">
                            Salesfunnel
                            Salesrenewal
                            Salesrate
                        </Link> */}
                        <Menu.Item key="7">销售漏斗
                            <Link to="/App/Busines/Salesfunnel" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="8">新增商机分析
                            <Link to="/App/Busines/Salesrenewal" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="9">商机转化率分析
                            <Link to="/App/Busines/Salesrate" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub3" icon={<FundOutlined />} title="员工业绩分析">
                        {/* <Link to="/App/Client/" className="a">
                            Employeenumber
                            Employeecontract
                            Employeeamount
                            Employeereceivable
                        </Link> */}
                        <Menu.Item key="10">合同数量分析
                            <Link to="/App/Busines/Employeenumber" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="11">合同金额分析
                            <Link to="/App/Busines/Employeecontract" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="12">回款金额分析
                            <Link to="/App/Busines/Employeeamount" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="13">合同汇总表
                            <Link to="/App/Busines/Employeereceivable" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub4" icon={<UserAddOutlined />} title="客户画像分析">
                        {/* <Link to="/App/Client/" className="a">
                            Customersurban
                            Customersindustry
                            Customerslevel
                            Customerssource
                        </Link> */}
                        <Menu.Item key="14">城市分布分析
                            <Link to="/App/Busines/Customersurban" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="15">客户行业分析
                            <Link to="/App/Busines/Customersindustry" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="16">客户级别分析
                            <Link to="/App/Busines/Customerslevel" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="17">客户来源分析
                            <Link to="/App/Busines/Customerssource" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub5" icon={<AppstoreOutlined />} title="产品分析">
                        {/* <Link to="/App/Client/" className="a">
                            Productssales
                            Productsvolume
                        </Link> */}
                        <Menu.Item key="18">产品销售情况统计
                            <Link to="/App/Busines/Productssales" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="19">产品分类销量分析
                            <Link to="/App/Busines/Productsvolume" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub6" icon={<BarChartOutlined />} title="排行榜">
                        {/* <Link to="/App/Client/" className="a">
                            Listcontract
                            Listamount
                            Listofferid
                            Listsales
                            Listnewclient
                            Listnewcontact
                            Listnumber
                            Listfollows
                            Listtrips
                        </Link> */}
                        <Menu.Item key="20">合同金额排行
                            <Link to="/App/Busines/Listcontract" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="21">回款金额排行
                            <Link to="/App/Busines/Listamount" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="22">密约合同排行
                            <Link to="/App/Busines/Listofferid" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="23">产品销量排行
                            <Link to="/App/Busines/Listsales" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="24">新增客户数排行
                            <Link to="/App/Busines/Listnewclient" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="25">新增联系人数排行
                            <Link to="/App/Busines/Listnewcontact" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="26">跟进次数排行
                            <Link to="/App/Busines/Listnumber" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="27">跟进客户数排行
                            <Link to="/App/Busines/Listfollows" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="28">出差次数排行
                            <Link to="/App/Busines/Listtrips" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub7" icon={<LineChartOutlined />} title="办公分析">
                        {/* <Link to="/App/Client/" className="a">
                            officelog
                            officeapproval
                        </Link> */}
                        <Menu.Item key="29">日志分析
                            <Link to="/App/Busines/officelog" className="a"/>
                        </Menu.Item>
                        <Menu.Item key="30">审批分析
                            <Link to="/App/Busines/officeapproval" className="a"/>
                        </Menu.Item>
                    </SubMenu>
                    <Menu.Item key="31" icon={<span className="iconfont">&#xe608;</span>} title="业绩目标完成情况">
                        业绩目标完成情况
                        <Link to="/App/Busines/Browserperformance" className="a"/>
                        </Menu.Item>
                </Menu>
                <Button style={{ background: 'none' }} type="primary" onClick={toggleCollapsed} style={{ marginBottom: 16 }}>
                    {React.createElement(state.collapsed? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
                </div>
            </div>
            <div className="content_box">
                <div className="content clearfix">
                    <Switch>
                        <Route path="/App/Busines/Customerfollows" component={Customerfollows}/>
                        <Route path="/App/Busines/Customeratom" component={Customeratom}/>
                        <Route path="/App/Busines/Customerrate" component={Customerrate}/>
                        <Route path="/App/Busines/Customerseas" component={Customerseas}/>
                        <Route path="/App/Busines/Customerdeal" component={Customerdeal}/>
                        <Route path="/App/Busines/Salesfunnel" component={Salesfunnel}/>
                        <Route path="/App/Busines/Salesrenewal" component={Salesrenewal}/>
                        <Route path="/App/Busines/Salesrate" component={Salesrate}/>
                        <Route path="/App/Busines/Employeenumber" component={Employeenumber}/>
                        <Route path="/App/Busines/Employeecontract" component={Employeecontract}/>
                        <Route path="/App/Busines/Employeeamount" component={Employeeamount}/>
                        <Route path="/App/Busines/Employeereceivable" component={Employeereceivable}/>
                        <Route path="/App/Busines/Customersurban" component={Customersurban}/>
                        <Route path="/App/Busines/Customersindustry" component={Customersindustry}/>
                        <Route path="/App/Busines/Customerslevel" component={Customerslevel}/>
                        <Route path="/App/Busines/Customerssource" component={Customerssource}/>
                        <Route path="/App/Busines/Listcontract" component={Listcontract}/>
                        <Route path="/App/Busines/Listamount" component={Listamount}/>
                        <Route path="/App/Busines/Listofferid" component={Listofferid}/>
                        <Route path="/App/Busines/Listsales" component={Listsales}/>
                        <Route path="/App/Busines/Listnewclient" component={Listnewclient}/>
                        <Route path="/App/Busines/Listnewcontact" component={Listnewcontact}/>
                        <Route path="/App/Busines/Listnumber" component={Listnumber}/>
                        <Route path="/App/Busines/Listfollows" component={Listfollows}/>
                        <Route path="/App/Busines/Listtrips" component={Listtrips}/>
                        <Route path="/App/Busines/Productssales" component={Productssales}/>
                        <Route path="/App/Busines/Productsvolume" component={Productsvolume}/>
                        <Route path="/App/Busines/Officelog" component={Officelog}/>
                        <Route path="/App/Busines/Officeapproval" component={Officeapproval}/>
                        <Route path="/App/Busines/Browserperformance" component={Browserperformance}/>
                        <Route path="/App/Busines" component={Customertotal}/>
                    </Switch>
                </div>
            </div>
        </div>
    )
}

export default Busines