import '../components/listcontract.css'
import { ConfigProvider, DatePicker, Form, Space, Select} from 'antd';
import 'antd/dist/antd.css';
import ListConnent from '../components/listcontract_connent1'
import { useState } from 'react';
function Listcontract(){
    const [listcontractarr,set_listcontractarr] = useState(JSON.parse(localStorage.sb))
    console.log(listcontractarr);
    return(
        <div className="listcontract">
            <div className="listcontract_box1">
                <div className="listcontract_box1_header">
                    <div className="listcontract_box_header_box1">
                        <Form labelCol={{ span: 4 }} wrapperCol={{ span: 14 }} layout="horizontal">
                            <Select>
                                <Select.Option value="demo1 ">今天</Select.Option>
                                <Select.Option value="demo2 ">昨天</Select.Option>
                                <Select.Option value="demo3 ">本周</Select.Option>
                                <Select.Option value="demo4 ">上周</Select.Option>
                                <Select.Option value="demo5 ">本月</Select.Option>
                                <Select.Option value="demo6 ">上月</Select.Option>
                                <Select.Option value="demo7 ">本季度</Select.Option>
                                <Select.Option value="demo8 ">上季度</Select.Option>
                                <Select.Option value="demo9 ">本年</Select.Option>
                                <Select.Option value="demo0 ">去年</Select.Option>
                                <Select.Option value="demo11">自定义</Select.Option>
                            </Select>
                        </Form>
                    </div>
                    <div className="listcontract_box_header_box2">
                        <Form labelCol={{ span: 4 }} wrapperCol={{ span: 14 }} layout="horizontal">
                            <Select>
                                <Select.Option value="demo1">办公室</Select.Option>
                                <Select.Option value="demo2">一打杂部</Select.Option>
                            </Select>
                        </Form>
                    </div>
                    <div className="listcontract_box1_header_box3">
                        搜索
                    </div>
                </div>
                <div className="listcontract_box1_header_title">合同金额排行（按实际下单时间）</div>
                <div className="listcontract_box1_header_paih">
                    <ListConnent></ListConnent>
                </div>
                <div className="listcontract_box1_biaoge">
                    <div className="listcontract_box1_biaoge_div">
                    <div className="listcontract_box1_biaoge_dingwei clearfix">
                        <div>公司总排名</div>
                        <div>签订人</div>
                        <div>部门</div>
                        <div>合同金额(元)</div>
                    </div>
                        <div className="listcontract_box1_biaoge_box1">
                            <div>
                                {listcontractarr[0].userList.map((item,index)=>{
                                    return(
                                        <div key={index}>{index + 1}</div>
                                    )
                                })}
                            </div>
                            <div>
                                {listcontractarr[0].userList.map((item,index)=>{
                                    return(
                                        <div key={index}>{item.realname}</div>
                                    )
                                })}
                            </div>
                            <div>
                                {listcontractarr[0].userList.map((item,index)=>{
                                    return(
                                        <div key={index}>办公室</div>
                                    )
                                })}
                            </div>
                            <div>
                                {listcontractarr[0].userList.map((item,index)=>{
                                    return(
                                        <div key={index}>0.00</div>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Listcontract