// import {  } from 'antd';//选项卡
import '../view/task.css'
import React,{ useState } from 'react';
import { Input, DatePicker,Space , Select, ConfigProvider,Tabs} from 'antd';
import Time from '../components/rl_time'
import moment from 'moment';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';
import zhCN from 'antd/lib/locale/zh_CN';
moment.locale('zh-cn');
// import { Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
// import { PlusOutlined } from '@ant-design/icons';//侧边栏

const { TabPane } = Tabs;//选项卡

const { Search } = Input;//搜索框
// const { Option } = Select;//侧边栏

moment.locale('zh-cn');
  const onSearch = value => console.log(value);

 
function callback(key) {
    console.log(key);
  }
function Task(){
    const { Option } = Select;

    function handleChange(value) {
      console.log(`selected ${value}`);
    }
    const abb =JSON.parse(localStorage.task)
    
    // console.log(abb);
    const [back,setback] = useState(1)
    const [backa,setbacka] = useState(1)
    const [backs,setbacks] = useState(1)
    const [backd,setbackd] = useState(1)

    function task_aaa(){
        if(back===1){
            setback(2)
            setbacka(1)
            setbacks(1)
            setbackd(1)
        }else{
            setback(1)
        }
    }
    function task_bbb(){
        if(backs===1){
            setbacks(2)
            setback(1)
            setbacka(1)
            setbackd(1)
        }else{
            setbacks(1)
            
        }
    }
    function task_ccc(){
        if(backd===1){
            setbackd(2)
            setback(1)
            setbacka(1)
            setbacks(1)
            
        }else{
            setbackd(1)

        }
    }
    function task_ddd(){
        if(backa===1){
            setbacka(2)
            setback(1)
            
            setbacks(1)
            setbackd(1)
        }else{
            setbacka(1)

        }
    }
    const [task_listsss_list_dw_hidden,set_task_listsss_list_dw_hidden] = useState(0)
   
    function task_listsss_list_dw_box1_dw(){
        set_task_listsss_list_dw_hidden(0)
    }
    const [task_listsss_list_dw_hidden_index,set_task_listsss_list_dw_hidden_index] = useState('')
    const [task_listsss_list_dw_hidden_index2,set_task_listsss_list_dw_hidden_index2] = useState('')
    // function task_listsss_list_dw_hidden_indexbtn(index){
    // }
    function task_listsss_list_dw_hiddens(index){
        set_task_listsss_list_dw_hidden(1)
        set_task_listsss_list_dw_hidden_index(abb[index].task_name)
        set_task_listsss_list_dw_hidden_index2(abb[index].main_user.img)
    }

    function onChange(date, dateString) {
    console.log(date, dateString);
    }
    const [task_listsss_btn_aa,set_task_listsss_btn_aa] = useState(0)
    function task_listsss_btn_a(){
        set_task_listsss_btn_aa(1)
    }
    function task_listsss_btn_b(){
        set_task_listsss_btn_aa(0)
    }

    const [left_btn_num,set_left_btn_num] = useState(0)
    function left_btn(){
        set_left_btn_num(1)
    }

    const [left_btn_nums,set_left_btn_nums] = useState(0)
    function create_btn2(){
        set_left_btn_nums(0)
        set_left_btn_num(0)
        
        // console.log(num);
    }
    return(
        <div className="task_crwa">
            <div className="task_crwa">
            <div className="center">
                <div className="box1">
                    <Tabs defaultActiveKey="1" onChange={callback}>
                        <TabPane tab="我的任务" key="1">
                            <Space direction="vertical">
                                <Search placeholder="搜索任务名称" onSearch={onSearch} style={{ width: 300 }} />
                            </Space>
                            <div className="menu">
                                <div className="shadow">
                                <span className="span">任务类型</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">我负责的</Option>
                                        <Option value="disabled"> 我创建的</Option>
                                        <Option value="Yiminghe">我参与的</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                <span className="span">状态</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">正在进行</Option>
                                        <Option value="disabled"> 已完成</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                    <span className="span">优先级</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">高</Option>
                                        <Option value="disabled"> 中</Option>
                                        <Option value="Yiminghe">低</Option>
                                        <Option value="Yiminghe">无</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                    <span className="span">截止时间</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">今天到期</Option>
                                        <Option value="disabled"> 明天到期</Option>
                                        <Option value="Yiminghe">一周到期</Option>
                                        <Option value="Yiminghe">一月到期</Option>
                                    </Select>
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab="我下属的任务" key="2">
                            <Space direction="vertical">
                                <Search placeholder="搜索任务名称" onSearch={onSearch} style={{ width: 300 }} />
                            </Space>
                            <div className="menu">
                                <div className="shadow">
                                <span className="span">任务类型</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">我负责的</Option>
                                        <Option value="disabled"> 我创建的</Option>
                                        <Option value="Yiminghe">我参与的</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                <span className="span">状态</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">正在进行</Option>
                                        <Option value="disabled"> 已完成</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                    <span className="span">优先级</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">高</Option>
                                        <Option value="disabled"> 中</Option>
                                        <Option value="Yiminghe">低</Option>
                                        <Option value="Yiminghe">无</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                    <span className="span">截止时间</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">今天到期</Option>
                                        <Option value="disabled"> 明天到期</Option>
                                        <Option value="Yiminghe">一周到期</Option>
                                        <Option value="Yiminghe">一月到期</Option>
                                    </Select>
                                </div>
                                <div className="shadow">
                                    <span className="span">负责人</span>
                                    <Select defaultValue="全部" style={{ width: 100 }} onChange={handleChange}>
                                        <Option value="jack">全部</Option>
                                        <Option value="lucy">11</Option>
                                    </Select>
                                </div>
                            </div>
                            <div className="task_listsss">
                                <div className="task_listsss_os">
                                    {abb.map((item,index)=>{
                                        return(
                                            <div className="task_listsss_list" onClick={()=>{
                                                task_listsss_list_dw_hiddens(index)
                                            }} key={index}>
                                                <p><input type="checkbox"/> {item.task_name} <img src={item.main_user.img}/></p>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </TabPane>
                    </Tabs>
                    <button className="xian" type="button" onClick={left_btn}>新建任务</button>
                </div>
            </div>
            <div className={task_listsss_list_dw_hidden===1?'task_listsss_list_dw':'task_listsss_list_dw_hiddens'}>
                <div className="task_listsss_list_dw_box1">
                    <ul className="task_listsss_list_dw_box1_ul">
                        <li><span className="iconfont">&#xe617;</span>优先级</li>
                        <li><span className="iconfont">&#xe681;</span>标签</li>
                        <li><span className="iconfont">&#xe6ea;</span>子任务</li>
                        <li><span className="iconfont">&#xe71a;</span>附件</li>
                    </ul>
                    <span onClick={task_listsss_list_dw_box1_dw} className="iconfont task_listsss_list_dw_box1_dw">&#xe63f;</span>
                </div>
                <div className="task_listsss_list_dw_bottom">
                    <div className="task_listsss_list_bottom clearfix">
                        <p><input type="checkbox"/>{task_listsss_list_dw_hidden_index}</p>
                        <div><span>负责人： </span><img className="task_listsss_list_bottom_img" src={task_listsss_list_dw_hidden_index2}/></div>
                    </div>
                    <div className="task_listsss_list_bottom2 clearfix">
                        <div className="task_listsss_list_dw_bottom_biaoq">
                            <span className="iconfont">&#xe711;</span><span>标签</span>
                        </div>
                        <div className="task_listsss_list_bottom3">
                            <span>截止日期： </span><span className="iconfont task_listsss_icons">&#xe6f5;</span>
                            <div className="task_listsss_list_bottom3_show">
                                <ConfigProvider locale={zhCN}>
                                    <Space direction="vertical" className="task_listsss_list_bottom3_yinc">
                                        <DatePicker onChange={onChange} />
                                    </Space>,
                                </ConfigProvider>
                            </div>
                        </div>
                    </div>
                    <div className="task_listsss_list_bottom4">
                        <p>暂无描述</p>
                        <p><span className="iconfont">&#xe68e;</span>参与人：<img className="task_listsss_list_bottom_img" src={task_listsss_list_dw_hidden_index2}/><span className="iconfont task_listsss_list_bottom4_icon1">&#xe711;</span></p>
                        <p><span className="iconfont">&#xe637;</span>关联业务</p>
                        <div className="task_listsss_list_bottom4_box1"><span className="iconfont">&#xe71a;</span>关联业务</div>
                        <div className="task_listsss_list_bottom4_box2"><span className="iconfont">&#xe62f;<span>子任务  (0/0)</span></span><p className="task_listsss_list_bottom4_box2_P"></p></div>
                        <div className="task_listsss_list_bottom4_box1"><span className="iconfont">&#xe71a;</span>添加子任务</div>
                    </div>
                </div>
                <div className="task_listsss_list_dw_bottom_box">
                    <div className="task_listsss_list_dw_bottom_box_list clearfix">
                        <div onClick={task_listsss_btn_a} className={task_listsss_btn_aa===0?'':'task_listsss_list_dw_bottom_box_list_iconsss'}><span className="iconfont">&#xe619;</span>活动</div>
                        <div>|</div>
                        <div onClick={task_listsss_btn_b} className={task_listsss_btn_aa===1?'':'task_listsss_list_dw_bottom_box_list_iconsss'}><span className="iconfont">&#xe705;</span>评论</div>
                    </div>
                    <div className={task_listsss_btn_aa===0?'task_listsss_list_bottom_box_img_hidden':'task_listsss_list_bottom_box_img clearfix'}>
                        <div className="task_listsss_list_bottom_box_img1"><img src="http://crm.cimns.com/public/uploads/20210408/thumb_499ee1871eab265f3923399da681f087.jpg" /></div>
                        <div className="task_listsss_list_bottom_box_img2">添加评论</div>
                    </div>
                    <div className={task_listsss_btn_aa===1?'task_listsss_list_bottom_box_img_hidden':'task_listsss_list_bottom_box_img clearfix'}>
                        <div className="task_listsss_list_bottom_box_img5"><img src="http://crm.cimns.com/public/uploads/20210408/thumb_499ee1871eab265f3923399da681f087.jpg" /></div>
                        <div className="task_listsss_list_bottom_box_img3">任海龙 任务标记结束</div>
                        <p className="task_listsss_list_bottom_box_img4">04-12 14:11</p>
                    </div>
                    <div className="task_listsss_list_dw_bottom_box3">金文 创建于 2021-04-10 23:14:07</div>
                </div>
            </div>
            </div>
            <div className={left_btn_num===1?'cent_box':'cent_box_hidden'}>
                <div className="nr">
                    <div className="work_tianjia">
                        <div className="nr_box">
                            <div className="header_box">
                                <h4>新建任务</h4>
                                <div className="work_liter_x">
                                    <div className="rw">
                                        <div className="one">
                                            <h6>任务名称</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                        <div className="one">
                                            <h6>负责人</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                    </div>
                                    <div className="work_ch clearfix">
                                        <div className="create_lefts">
                                            <p className="create_div_p"><span className="create_div_span">*</span>开始时间</p>
                                            <Time></Time>
                                        </div>
                                        <div className="create_rights">
                                            <p className="create_div_p"><span className="create_div_span">*</span>结束时间</p>
                                            <Time></Time>
                                        </div>
                                    </div>
                                    <div className="you">
                                        <h6 className="you_ki">优先级</h6>
                                        <span onClick={task_aaa} className={back===2?'hong bg-h':'hong'} >高</span>
                                        <span onClick={task_bbb} className={backs===2?'hong bg-c':'cheng'}>中</span>
                                        <span onClick={task_ccc} className={backd===2?'hong bg-l':'lan'}>低</span>
                                        <span onClick={task_ddd} className={backa===2?'hong bg-hui':'hui'}>无</span>
                                    </div>
                                    <div className="ms">
                                        <h6 className="ma_se">任务描述</h6>
                                    </div>
                                    <textarea autoComplete="off" className="wokr_cll"></textarea>
                                    <div className="gl">
                                        <p>关联业务</p>
                                    </div>
                                </div>
                                <span onClick={create_btn2} className="iconfont create_icon">&#xe63f;</span>
                                <div className="create_bc">
                                    <div className="create_bcqx1">保存</div>
                                    <div onClick={create_btn2} className="create_bcqx2">取消</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
       </div>
    )
}

export default Task