import { useState } from 'react'
import Time from '../components/rl_time';
import '../components/jobstask.css'
function Jobstask(){
    const [jobstask_newbox_num,set_jobstask_newbox_arr] = useState(0)
    function jobstask_newbox_start(){
        set_jobstask_newbox_arr(1)
    }
    function set_jobstask_newbox_arr_end(){
        set_jobstask_newbox_arr(0)
    }
    const [back,setback] = useState(1)
    const [backa,setbacka] = useState(1)
    const [backs,setbacks] = useState(1)
    const [backd,setbackd] = useState(1)

    function task_aaa(){
        if(back===1){
            setback(2)
            setbacka(1)
            setbacks(1)
            setbackd(1)
        }else{
            setback(1)
        }
    }
    function task_bbb(){
        if(backs===1){
            setbacks(2)
            setback(1)
            setbacka(1)
            setbackd(1)
        }else{
            setbacks(1)
            
        }
    }
    function task_ccc(){
        if(backd===1){
            setbackd(2)
            setback(1)
            setbacka(1)
            setbacks(1)
            
        }else{
            setbackd(1)

        }
    }
    function task_ddd(){
        if(backa===1){
            setbacka(2)
            setback(1)
            
            setbacks(1)
            setbackd(1)
        }else{
            setbacka(1)

        }
    }
    return(
        <div className="Jobstask">
            <div className={jobstask_newbox_num===1?'cent_box':'cent_box_hidden'}>
                <div className="nr">
                    <div className="work_tianjia">
                        <div className="nr_box">
                            <div className="header_box">
                                <h4>新建任务</h4>
                                <div className="work_liter_x">
                                    <div className="rw">
                                        <div className="one">
                                            <h6>任务名称</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                        <div className="one">
                                            <h6>负责人</h6>
                                            <input type="text" className="int"/>
                                        </div>
                                    </div>
                                    <div className="work_ch clearfix">
                                        <div className="create_lefts">
                                            <p className="create_div_p"><span className="create_div_span">*</span>开始时间</p>
                                            <Time></Time>
                                        </div>
                                        <div className="create_rights">
                                            <p className="create_div_p"><span className="create_div_span">*</span>结束时间</p>
                                            <Time></Time>
                                        </div>
                                    </div>
                                    <div className="you">
                                        <h6 className="you_ki">优先级</h6>
                                        <span onClick={task_aaa} className={back===2?'hong bg-h':'hong'} >高</span>
                                        <span onClick={task_bbb} className={backs===2?'hong bg-c':'cheng'}>中</span>
                                        <span onClick={task_ccc} className={backd===2?'hong bg-l':'lan'}>低</span>
                                        <span onClick={task_ddd} className={backa===2?'hong bg-hui':'hui'}>无</span>
                                    </div>
                                    <div className="ms">
                                        <h6 className="ma_se">任务描述</h6>
                                    </div>
                                    <textarea autoComplete="off" className="wokr_cll"></textarea>
                                    <div className="gl">
                                        <p>关联业务</p>
                                    </div>
                                </div>
                                <span onClick={set_jobstask_newbox_arr_end} className="iconfont create_icon">&#xe63f;</span>
                                <div className="create_bc">
                                    <div className="create_bcqx1">保存</div>
                                    <div onClick={set_jobstask_newbox_arr_end} className="create_bcqx2">取消</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="Jobstask_box">
                <h3>我的任务</h3>
                <div className="Jobstask_box_div">
                    <div>
                        <div className="Jobstask_title">
                            <p className="Jobstask_title_p1">收件箱<span>0 / 0</span></p>
                            <p className="Jobstask_title_p2"></p>
                        </div>
                        <div className="Jobstask_newbox" onClick={jobstask_newbox_start}>
                            <p><span className="iconfont">&#xe711;</span>新建任务</p>
                        </div>
                    </div>
                    <div>
                        <div className="Jobstask_title">
                            <p className="Jobstask_title_p1">今天要做<span>0 / 0</span></p>
                            <p className="Jobstask_title_p2"></p>
                        </div>
                        <div className="Jobstask_newbox" onClick={jobstask_newbox_start}>
                            <p><span className="iconfont">&#xe711;</span>新建任务</p>
                        </div>
                    </div>
                    <div>
                        <div className="Jobstask_title">
                            <p className="Jobstask_title_p1">下一步要做<span>0 / 0</span></p>
                            <p className="Jobstask_title_p2"></p>
                        </div>
                        <div className="Jobstask_newbox" onClick={jobstask_newbox_start}>
                            <p><span className="iconfont">&#xe711;</span>新建任务</p>
                        </div>
                    </div>
                    <div>
                        <div className="Jobstask_title">
                            <p className="Jobstask_title_p1">以后要做<span>0 / 0</span></p>
                            <p className="Jobstask_title_p2"></p>
                        </div>
                        <div className="Jobstask_newbox" onClick={jobstask_newbox_start}>
                            <p><span className="iconfont">&#xe711;</span>新建任务</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Jobstask