import './App.css';
import axios from 'axios'
import { Route, Switch } from "react-router";
import { Link } from "react-router-dom";
import Office from './view/office'
import Client from './view/client'
import Busines from './view/busines'
import Project from './view/project'
import {useState} from 'react'

axios.interceptors.request.use((config)=>{
  if(localStorage.login){
    var login = JSON.parse(localStorage.login)
    config.headers={
      authKey:login.authKey,
      sessionId:login.sessionId
      }
    return config
  }else{
    return config
  }
})


function App(props) {
  if(!localStorage.login){
    props.history.push('/')
  }else{
    // var login = JSON.parse(localStorage.login)
  }
  axios.post('http://crm.cimns.com/index.php/oa/index/index',{
    type:0,
    page:1,
    limit:15
  }).then((data)=>{
    // console.log(data);
    
    if(data.data.code === 101){
      // console.log(data.data.error)
    }
  },(err)=>{
    // console.log(err)
  })

  let [index,setindex] = useState(JSON.parse(localStorage.index))
  // console.log(index.index);
  function btn(){
    setindex({
      index:2
    })
    localStorage.index = JSON.stringify({index:2})
    //人
    // axios.post('http://crm.cimns.com/index.php/admin/users/getUserList',)
    // .then(res => {
    //   localStorage.dashboard.push(JSON.stringify(res.data.data))
    //   console.log(res.data.data)
    // })
    //时间
    axios.post('http://crm.cimns.com/index.php/crm/index/saletrend',)
    .then(res => {
      localStorage.dashboarda = JSON.stringify(res.data.data.list)
      console.log(res.data.data)
      localStorage.dashboard = JSON.stringify({index:2})
      console.log(res.data.data)
    })
    //漏斗
    axios.post('http://crm.cimns.com/index.php/crm/business/statusList',)
    .then(res => {
    localStorage.dashboardb = JSON.stringify(res.data.data)
    console.log(res.data.data)
    })
    // axios.post('http://crm.cimns.com/index.php/crm/index/funnel',)
    // .then(res => {
    //   console.log(res)
    // })
    // axios.post('http://crm.cimns.com/index.php/crm/index/indexList',)
    // .then(res => {
    //   console.log(res)
    // })
  }
  function btn1(){
    setindex({
      index:1
    })
    localStorage.index = JSON.stringify({index:1})
  }
  function btn2(){
    setindex({
      index:3
    })
    localStorage.index = JSON.stringify({index:3})
  }
  function btn3(){
    setindex({
      index:4
    })
    localStorage.index = JSON.stringify({index:4})
  }
  function tcdl(){
    localStorage.clear()
    props.history.push('/')
  }
  return (
    <div className="uo">
        <div className="tb clearfix">
            <div className="logo">
              <img src="http://crm.cimns.com/static/img/logo.3e34073.png"/>
            </div>
            <Link className={index.index==1?'active':"tite"} onClick={btn1} to="/App"><span className="iconfont">&#xe625;</span>办公</Link>
            <Link className={index.index==2?'active':"tite"} onClick={btn}  to="/App/Client"><span className="iconfont">&#xe68e;</span>客户管理</Link>
            <Link className={index.index==3?'active':"tite"} onClick={btn2} to="/App/Busines"><span className="iconfont">&#xe673;</span>商业智能</Link>
            <Link className={index.index==4?'active':"tite"} onClick={btn3} to="/App/Project"><span className="iconfont">&#xe614;</span>项目管理</Link>
            <span className="app-tc" onClick={tcdl}>退出登录</span>
        </div>
        <Switch>
            <Route path="/App/Client" component={Client} />
            <Route path="/App/Busines" component={Busines} />
            <Route path="/App/Project" component={Project} />
            <Route path="/App/" component={Office} />
        </Switch>
    </div>
  );
}

export default App;
