
import {useState} from 'react'
import './through.css'
import { RightOutlined} from '@ant-design/icons';

function Through(){

    const arr =JSON.parse(localStorage.sb)
// console.log(arr)
    
        let userlist = []
        if(arr.length>0){
            for (let i = 0; i < arr[0].userList.length; i++) {
                userlist.push(arr[0].userList[i].realname)
            }
        }
        userlist.sort((a, b) => a.localeCompare(b, 'zh-Hans-CN', {sensitivity: 'accent'}))
        let nweuserlist=[]
       for (let i = 0; i < userlist.length; i++) {
        nweuserlist.push({
            id:arr[0].userList[i].id,
            realname:userlist[i],
            post:arr[0].userList[i].post,
            mobile:arr[0].userList[i].mobile
        })
       }
        const [num,setnum] = useState(1)
        function cor(){
            setnum(1)
        }
        function cor2(){
            setnum(2)
        }

        const [down,setdown]=useState(1)
        const [down2,setdown2]=useState(1)
        function fun_down(){
            if(down===1){
                setdown(2)
            }else{
                setdown(1)
            }
               
        }
        function fun_down2(){
            if(down2===1){
                setdown2(2)
            }else{
                setdown2(1)
            }
        }

        const [ssval,setssval] = useState([])
        
        function changeval(dom){
            setssval(dom.target.value)
            // console.log(ssval);
        }
        let ss = []
        const onKeyup=(e)=>{
            if(e.keyCode === 13) {
                for (let i = 0; i <userlist.length; i++) {
                    if(ssval===userlist[i]){
                        ss.push({
                            id:arr[0].userList[i].id,
                            realname:userlist[i],
                            post:arr[0].userList[i].post,
                            mobile:arr[0].userList[i].mobile
                        })                      
                    }
                }
                console.log(ss);
            }
        }
    return(
        <div className="through_box"> 
            <div className="through">
                    <ul className="title">
                        <li onClick={cor} className={num===1?'title_color':''}>
                            员工
                            <span></span>
                        </li>
                        <li onClick={cor2} className={num===2?'title_color':''}>
                            部门
                            <span></span>
                        </li>
                    </ul>
                    <div className="input_b">
                        <div className="input_box">
                            <input type="text" placeholder="搜索成员" val={ssval} onChange={changeval} onKeyUp={onKeyup}/>
                            <span className="iconfont">&#xe612;</span>
                        </div>
                    </div>
                <div className={num===1?'':'title_block userlist_box'}>
                    <div className="userlist overflow">
                        <div>
                            {nweuserlist.map((ite,indexs)=>{
                            return(
                                    <div key={indexs} className="staff">
                                        <div className="k-realname">
                                            {ite.realname}
                                        </div>
                                        <div className="list-right">
                                            <span className='list-right-span'><span className="iconfont">&#xe637;</span>{arr[0].name}</span>
                                            <span className='list-right-span'><span className="iconfont">&#xe617;</span>{ite.post}</span>
                                            <span className='list-right-span'><span className="iconfont">&#xe621;</span>{ite.mobile}</span>
                                        </div>
                                    </div>
                                )
                            })}
                                    
                        </div>
                       
                    </div>
                </div>
                <div className={num===2?'':'title_block'}>
                   <div onClick={fun_down} className={down===1?'userlist overflow':'userlist'}>
                        <div className="userlist-box-small">
                            <div className="el-collapse-item__header">
                                <h3 onClick={fun_down} className={down===1?'el-collapse-item__header_title fun_down':'el-collapse-item__header_title'}>
                                    <span className="anticon"><RightOutlined /></span>
                                    {[arr[0].name]}
                                    <span className="marg">({[arr[0].userList.length]})</span>
                                </h3>
                            </div>
                            <div onClick={fun_down} className={down===1?'':'no'}>
                                    {nweuserlist.map((i,index)=>{
                                        return(
                                            <div key={index} className="staff">
                                                <div className="k-realname">
                                                    {i.realname}
                                                </div>
                                                <div className="list-right">
                                                    <span className='list-right-span'><span className="iconfont">&#xe637;</span>{arr[0].name}</span>
                                                    <span className='list-right-span'><span className="iconfont">&#xe617;</span>{i.post}</span>
                                                    <span className='list-right-span'><span className="iconfont">&#xe621;</span>{i.mobile}</span>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                   </div>
                   <div onClick={fun_down2} className={down2===2?'userlist overflow':'userlist'}>
                    <div className="el-collapse-item__header">
                                <h3 onClick={fun_down2} className={down2===2?'el-collapse-item__header_title fun_down':'el-collapse-item__header_title'}>
                                    <span className="anticon"><RightOutlined /></span>
                                    {[arr[1].name]}
                                    <span className="marg">({[arr[1].userList.length]})</span>
                                </h3>
                            </div>
                    </div>
                </div>
                <div className="tab-pane"></div>
            </div>
        </div>
    )
}
export default Through