import {useState} from 'react'
import { StarOutlined, StarFilled, StarTwoTone,NodeIndexOutlined,FundOutlined,PlusOutlined,LinkOutlined} from '@ant-design/icons';
import { Calendar,ConfigProvider } from 'antd';
import zhCN from 'antd/lib/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';

import './approve.css'
function Approve(){
// moment.locale('zh-cn');
    const [arr,setarr] = useState(JSON.parse(localStorage.approve))
    const [arr_list,setarr_list] = useState(JSON.parse(localStorage.sb))
    console.log(arr);
    
    const [arr2,setarr2] = useState([
        {title:"全部"},
        {title:"待审"},
        {title:"审批中"},
        {title:"通过"},
        {title:"失败"},
        {title:"撤回"},
    ])
    const [arr3,setarr3] = useState([
        {title:"待我审批的"},
        {title:"我已审批的"},
    ])
    // console.log(arr);
    const [approve_title_1,setapprove_title_1] = useState(1)
    // const [input_none4,setinput_none4] = useState(1)
    function approveTitle_1(){
        setapprove_title_1(1)
        
    }
    function approveTitle_2(){
        setapprove_title_1(2)
      
    }

    const [input_value,setinput_value] = useState('')
    const [input_value2,setinput_value2] = useState('')
    const [input_none,setinput_none] = useState(1)
    const [input_none2,setinput_none2] = useState(1)
    const [input_none3,setinput_none3] = useState(1)
    // function inputValue(event){
    //     let a = event.target.value
    //     console.log(a);
    //     setinput_value(arr[input_value_index].title)
    // }
    function approve_box_btn_select_input(){
        if(input_none===1){
            setinput_none(2)
        }else{
            setinput_none(1)
        }
        // console.log(input_none);
        
    }
    function approve_box_btn_select_input2(){
        if(input_none2===1){
            setinput_none2(2)
        }else{
            setinput_none2(1)
        }
        // console.log(input_none);
        
    }
    function approve_box_btn_select_input3(){
        if(input_none3===1){
            setinput_none3(2)
        }else{
            setinput_none3(1)
        }
        // console.log(input_none);
        
    }

    function input_list(index){
        setinput_value(arr[index].title)
        setinput_none(1)
    }
    function input_list2(index){
        setinput_value2(arr2[index].title)
        setinput_none2(1)
    }
    function onPanelChange(value, mode) {
        console.log(value, mode);
      }
      const [approve_box_qx,setapprove_box_qx] = useState(1)
    function approve_box__qx(){
        setapprove_box_qx(2)
    } 
    function approve_box__qx2(){
        setapprove_box_qx(1)
    }
    const [qx_el_card__body,setqx_el_card__body] = useState(1)
    function funqx_el_card__body(){
        
        if(qx_el_card__body===1){
            setqx_el_card__body(2)
        }else{
            setqx_el_card__body(1)
        }
        setapprove_box_qx(1)
    }
    const [inpu,setinpu] = useState(1)
    function oninpu(){
        if(inpu===1){
            setinpu(2)
        }else{
            setinpu(1)
        }
    }
    const [btnsnum,setbtnsnum] = useState(0)
    function btnsss(){
        if (btnsnum===0) {
            setbtnsnum(1)
        }else{
            setbtnsnum(0)
        }
    }
    return(
        <div className='approve_box'>
            <div className="approve_box_w">
                <div className="approve_box_title clearfix">
                    <ul className="approve_box_title__active-bar">
                        <li onClick={approveTitle_1} className={approve_title_1===1?'approve_box_title__active-bar_color':''}>
                            我发起的
                            <span></span>
                        </li>
                        <li onClick={approveTitle_2} className={approve_title_1===2?'approve_box_title__active-bar_color':''}>
                            我审批的
                            <span></span>
                        </li>
                    </ul>
                    <div className="approve_box_btn-select">
                        <div className="approve_box_btn-select_sp">审批类型</div>
                        <div className="approve_box_btn-select_input">
                            <input type="text" value={input_value} onClick={approve_box_btn_select_input} placeholder="全部" />
                            {/* onChange={inputValue} */}
                            <div className={input_none===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none'}>
                                
                                {
                                    arr.map((item,index)=>{
                                        return(
                                            <div key={index}>
                                                <div className="approve_box_btn-select_input_list_sm" onClick={()=>{input_list(index)}}>{item.title}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <span className={input_none===1?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe61f;</span>
                            <span className={input_none===2?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe689;</span>
                        </div>
                        <div className="approve_box_btn-select_xj" onClick={approve_box__qx}>新建审批</div>
                    </div>
                </div>
                <div >
                    <div className="select-group">
                        <div className="select-group-sh">审核状态</div>
                        <div className={approve_title_1===1?'approve_box_btn-select_input el-input--suffix-block':'approve_box_btn-select_input el-input--suffix-none'}>
                            <input type="text" value={input_value2} onClick={approve_box_btn_select_input2} placeholder="全部" />
                            {/* onChange={inputValue} */}
                            <div className={input_none2===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none'}>
                                
                                {
                                    arr2.map((item,index)=>{
                                        return(
                                            <div key={index}>
                                                <div className="approve_box_btn-select_input_list_sm" onClick={()=>{input_list2(index)}}>{item.title}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <span className={input_none2===1?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe61f;</span>
                            <span className={input_none2===2?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe689;</span>
                        </div>
                        <div className={approve_title_1===2?'approve_box_btn-select_input el-input--suffix-block':'approve_box_btn-select_input el-input--suffix-none'}>
                            <input type="text" value={input_value2} onClick={approve_box_btn_select_input2} placeholder="待我审批的" />
                            {/* onChange={inputValue} */}
                            <div className={input_none2===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block approve_box_btn-select_input_list_bottom':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none approve_box_btn-select_input_list_bottom'}>
                                {
                                    arr3.map((item,index)=>{
                                        return(
                                            <div key={index}>
                                                <div className="approve_box_btn-select_input_list_sm" onClick={()=>{input_list2(index)}}>{item.title}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <span className={input_none2===1?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe61f;</span>
                            <span className={input_none2===2?'iconfont approve_box_btn-select_input_ico':'iconfont'}>&#xe689;</span>
                        </div>
                        <div className="select-group-sh">发起时间</div>
                        <div className="approve_box_btn-select_inputrili">
                            <div onClick={approve_box_btn_select_input3} className="select-group-rq">
                                <div className="iconfont">&#xe6f5;</div>
                                <div className="approve_box_btn-select_input_left">
                                开始日期
                                </div>
                                <div className="select-group-rq-xian">-</div>
                                <div className="approve_box_btn-select_input_right">
                                结束日期
                                </div>
                            </div>
                        
                            <div className={input_none3===2?'approve_box_btn-select_input_list approve_box_btn-select_input_list_block':'approve_box_btn-select_input_list approve_box_btn-select_input_list_none'}>
                                <ConfigProvider locale={zhCN}>
                                    <div className="site-calendar-demo-card">
                                        <Calendar fullscreen={false} onPanelChange={onPanelChange} />
                                    </div>
                                </ConfigProvider>
                            </div>
                        </div> 
                    </div>
                    <div className='examine-list-boxmy'>没有更多了</div>  
                
                </div>   
                <div className={approve_box_qx===1?'approve_box_bac_none approve_box_bac':'approve_box_bac approve_box_bac_block'}>
                    <div className="approve_box_bac_wigth">
                        <h3 className='approve_box_bac_wigth_h3'>审批类型 <span className="iconfont" onClick={approve_box__qx2}>&#xe63f;</span></h3>
                        <p className="approve_box_bac_text">请选择您的审批类型（管理后台可自定义配置审批类型）</p>
                        <ul className="approve_box_bac_const">
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe614;</span>
                                <p>普通审批</p>
                            </li>
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe695;</span>
                                <p>请假审批</p>
                            </li>
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe600;</span>
                                <p>出差审批</p>
                            </li>
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe6f5;</span>
                                <p>加班审批</p>
                            </li>
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe66d;</span>
                                <p>差旅审批</p>
                            </li>
                            <li onClick={funqx_el_card__body}>
                                <span className='iconfont'>&#xe604;</span>
                                <p>借款审批</p>
                            </li>
                        </ul>
                    </div>
                </div>  
                <div className={qx_el_card__body===2?'qx_el-card__body  qx_el-card__body_didlock':'qx_el-card__body'}>
                    <div className="qx_el-card__body_box">
                        <h3 className='approve_box_bac_wigth_h3'>新建普通审批<span className="iconfont" onClick={funqx_el_card__body}>&#xe63f;</span></h3>
                        <div className="qx_el-card__body_content">
                            <div>
                                <p className="qx_el-card__body_section-title">基本信息</p>
                                <div className="qx_el-card__body_crm-create ">
                                    <div className="qx_el-card__body_crm-create_title crm-create_title_rig">
                                        <h6><span>*</span>审批内容</h6>
                                        <div className="qx_el-card__body_crm-create_input input_line_h_l">
                                            <input type="text" />
                                        </div>
                                    </div>
                                    <div className="qx_el-card__body_crm-create_title crm-create_title_lef">
                                        <h6>备注</h6>
                                        <div className="qx_el-card__body_crm-create_input input_line_h_r">
                                            <input type="text" />
                                        </div>
                                    </div>
                                </div>
                                <div>
                                    <div  className="el-upload--picture-card">
                                        <h4><span><FundOutlined /></span>添加图片</h4>
                                        <div className="el-upload--picture-card-jia">
                                           <PlusOutlined />
                                        </div>
                                    </div>
                                    <div  className="el-upload--picture-card">
                                        <h4><NodeIndexOutlined />添加附件</h4>

                                    </div>
                                    <div  className="el-upload--picture-card">
                                        <h4><LinkOutlined />关联业务</h4>
                                    </div>
                                </div>
                                <p className="qx_el-card__body_section-title">核实信息<span>授权审批人</span></p>
                                <div className="qx_el-card__body_crm_create_input_bo">
                                    <h6><span>*</span>审核人</h6>
                                    <div className="qx_el-card__body_crm_create_input_box"  onClick={btnsss}>
                                        <input type="text" placeholder="+添加员工" />
                                    </div>
                                    <div className={btnsnum===1?'create_cyr':'create_cyr_hidden'}>
                                        <div className="el-popover">
                                            <div className='colleagues-list-box'>
                                                {
                                                    arr_list[0].userList.map((item,index)=>{
                                                        return(
                                                            <div key={index} className="colleagues-list clearfix">
                                                                <div className="colleagues-list-input colleagues-list-input_top" onClick={oninpu}>
                                                                    <input type="checkbox" />
                                                                </div>
                                                                <div className="colleagues-list-cont">
                                                                    <div className="colleagues-list-img">
                                                                        <img src={item.thumb_img} />
                                                                    </div>
                                                                    <div className="colleagues-list-title">
                                                                        {item.realname}
                                                                    </div>
                                                                </div>
                                                            </div>

                                                        )
                                                    })
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="qx_el-card__body_crm_create_button">
                            <div className="qx_el-card__body_crm_create_button_l"  onClick={funqx_el_card__body}>保存</div>
                            <div className="qx_el-card__body_crm_create_button_r"  onClick={funqx_el_card__body}>取消</div>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}
export default Approve