import './archive.css'
import archive from '../img/content.png'
function Archive(){
    return(
        <div className="archive_box">
            <div className="archive_box_t">
                <h3 className="archive_box_title">归档项目统计</h3>
            </div>
            <div className="archive_box_content">
                <div className="archive_box_content_img">
                <img src={archive} alt=""/>
                <p className="archive_box_content_img_p">没有找到数据</p>
                </div>
                
            </div>
        </div>
    )
}
export default Archive