   function oninpu(){
       if(inpu===1){
           setinpu(2)
       }else{
           setinpu(1)
       }
   }
   const [btnsnum,setbtnsnum] = useState(0)
   function btnsss(){
       if (btnsnum===0) {
           setbtnsnum(1)
       }else{
           setbtnsnum(0)
       }
   }
